.box {
    width: 250px;
    height: auto;
    background-color: transparent;
    box-shadow: 0 0 1em rgb(0 0 0 / 30%);
    cursor: pointer;
}
.Poster {
    width: 100%;
}
.Poster img {
    width: 100%;
    height: 400px;
}
.moviesTitle {
    background-color:transparent;
}
.moviesTitle h1 {
    font-size: 16px;
    padding: 5px 10px;
    font-weight: 500;    
}
.box:hover {
    color: #fff;
}
